import React, { useState, useRef } from "react";
import "../../../map.css";
import useOutsideClick from "../../../useOutsideClick";

const Language = ({ selectedLanguage, setSelectedLanguage }) => {

  const [show, setSate] = useState(false);
  const ref = useRef();

  useOutsideClick(ref, () => {
    if (show) setSate(false);
  });

  const handleCheck = (i) => {

    setSelectedLanguage(i);
    localStorage.setItem("lang", i)
  };


  let style = {};

  return (
    <div className="language">
      <button className="btnLng" onClick={() => setSate(!show)}>
        {selectedLanguage == "arm" ?
          <img
            src={require("../../../icon/arm.svg").default}
            className="defaultImg"
            style={style}
          // style={{ display: visibleArm ? "inline" : "none" }}
          /> :
          <img
            src={require("../../../icon/eng.svg").default}
            className="defaultImg"
            style={style}
          // style={{ display: visibleEng ? "inline" : "none" }}
          />}


        <img className="arr" src={require("../../../icon/arrow.svg").default} />
      </button>
      {show && (
        <div ref={ref} className="iconLng">
          <ul>
            <li className="li4" onClick={() => handleCheck("arm")}>
              <div>
                <img src={require("../../../icon/arm.svg").default} />
              </div>
            </li>
            <li className="li4" onClick={() => handleCheck("en")}>
              <div>
                <img src={require("../../../icon/eng.svg").default} />
              </div>
            </li>
          </ul>
        </div>
      )}
    </div>
  );
};
export default Language;
